<template>
  <div>
    <v-app-bar
      v-scroll="onScroll"
      :color="!isScrolling ? 'transparent' : 'rgb(48, 51, 59)'"
      fixed
      flat
    >
      <span class="hidden-sm-and-up">
        <v-app-bar-nav-icon color="white" @click="sidebar = !sidebar"></v-app-bar-nav-icon>
      </span>
      <v-slide-x-reverse-transition>
        <!-- <h1 v-if="showLogo" style="color: #f64763" dark>Padhaai Tool</h1> -->

        <v-img
          v-if="showLogo"
          :src="require('@/assets/Dark.png')"
          class
          contain
          :max-width="[$vuetify.breakpoint.smAndDown ? '200' : '300']"
        />
        <v-img
          v-else
          :src="require('@/assets/Light.png')"
          class
          contain
          :max-width="[$vuetify.breakpoint.smAndDown ? '200' : '300']"
        />
      </v-slide-x-reverse-transition>
      <v-spacer />
      <v-btn
        :color="!isScrolling ? 'rgb(59, 89, 152)' : 'white'"
        class="hidden-xs-only"
        text
        small
        v-for="item in menuItems"
        :key="item.title"
        @click="$vuetify.goTo(item.ids)"
      >{{ item.title }}</v-btn>
    </v-app-bar>

    <v-navigation-drawer v-model="sidebar" app style="background-color: rgb(223, 227, 238)">
      <v-list shaped >
        <v-img class="ml-2" :src="require('@/assets/Light.png')" contain max-width="200" />
            <v-sheet color="rgb(223, 227, 238)" height="20"></v-sheet>
        <v-list-item-group color="primary">
          <v-list-item v-for="(item, i) in menuItems" :key="i">
            <v-list-item-content @click="$vuetify.goTo(item.ids)">
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  name: "CoreAppBar",
  data: () => ({
    showLogo: false,
    isScrolling: false,
    sidebar: false,
    menuItems: [
      { title: "Home", ids: "#home" },
      { title: "About Us", ids: "#about" },
      { title: "Features", ids: "#features" },
      // { title: "Pricing", ids: "#pricing" },
      { title: "Contact", ids: "#contact" },
    ],
  }),

  methods: {
    onScroll() {
      const offset = window.pageYOffset;
      this.isScrolling = offset > 50;
      this.showLogo = offset > 50;
    },
  },
};
</script>
